	module.exports = {"data":{"page":{"id":"C:/Users/opw/Desktop/workspace/blog/content/pages/2--projects/index.md absPath of file >>> MarkdownRemark","html":"<h3>Movie Recommendation Model with Apache Spark and Amazon EMR</h3>\n<img src=\"https://user-images.githubusercontent.com/10897048/45828658-a8eabe80-bd2b-11e8-89a6-c2df091de550.png\" style=\"max-width:600px;\">\n<p>This is my cloud computing course project. I built a movie recommendation model using Collaborative Filtering with Spark Machine Learning library. It was then deploy to Amazon EMR to effectively speed up the training process given a large dataset.</p>\n<p><a href=\"https://opw0011.github.io/COMP4651_Movie_Recommendation_Model/movie.html\" target=\"_blank\">Demo</a> |\n<a href=\"https://github.com/opw0011/COMP4651_Movie_Recommendation_Model\" target=\"_blank\">Github</a></p>\n<hr>\n<h3>Egg Saver ‚Äì A Pokemon Theme SVG Platform Game</h3>\n<img src=\"https://cloud.githubusercontent.com/assets/10897048/23100142/00b5fbec-f6b4-11e6-846c-24a7cc3c819d.gif\" alt=\"egg-saver\" style=\"max-width:600px;\">\n<p>This is a HTML platform game that was created with JavaScript, HTML, CSS and SVG. No gaming framework was used. </p>\n<p><a href=\"https://opw0011.github.io/SVG_Platform_Game/game.html\" target=\"_blank\">Demo</a> |\n<a href=\"https://github.com/opw0011/SVG_Platform_Game\" target=\"_blank\">Github</a></p>\n<hr>\n<h3>Q.Zeven ‚Äì An online Q&#x26;A platform for HKUST students</h3>\n<img src=\"https://user-images.githubusercontent.com/10897048/45829734-20215200-bd2e-11e8-9620-8e6509c20baf.png\" style=\"max-width:600px;\">\n<p><a href=\"http://opw0011.github.io/Question-Room/questionsJS/\" target=\"_blank\">Demo</a> |\n<a href=\"https://github.com/opw0011/Question-Room\" target=\"_blank\">Github</a></p>\n<hr>\n<h3>A lightweight npm library to parse curl request to JS</h3>\n<img src=\"https://user-images.githubusercontent.com/10897048/45830433-ae4a0800-bd2f-11e8-834e-7af1ed88eb47.png\" style=\"max-width:600px;\">\n<p>I spent some of my free time to try publish a npm library. This library can help covert cURL request to JavaScript object, which can help to do web crawling query quickly.</p>\n<p><a href=\"https://github.com/opw0011/curl2js\" target=\"_blank\">Github</a></p>\n<hr>\n<h3>Inverted Index Search Engine</h3>\n<p>It was built after attending Search Engine course in university. Basically, I went through from crawling data with a web crawler. Then, built inverted index using key-value database. Ranking the result with tf-idf and provide various search feature, like title search, phrase search and similar search etc. </p>\n<p><a href=\"https://github.com/opw0011/Search-Engine\" target=\"_blank\">Github</a></p>\n<hr>\n<p><em>Actually there is more, but I am too lazy to write them up ‚Ä¶ üòù You can check them on my Github</em></p>","frontmatter":{"title":"My Projects"}},"footnote":{"id":"C:/Users/opw/Desktop/workspace/blog/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>2018 ¬© Benny O</li>\n<li>built with React &#x26; Gatsby</li>\n<li>theme template <a href=\"https://github.com/greglobinski/gatsby-starter-personal-blog\">gatsby-starter-personal-blog</a></li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":""}}}},"pathContext":{"slug":"/projects/"}}

